<template>
  <el-row class="joke">
    <scroller :on-refresh="refresh" ref="jokeScroller">
      <el-row class="text-center white-back" v-loading="loading">
        <el-row v-for="(item, index) in jokes" :key="item.soureid">
          <el-row type="flex">
            <el-image :src="item.header"
                      v-viewer="{toolbar:false, title: false, navbar: false, keyboard: false}"
                      style="width: 50px; height: 50px;"></el-image>
            <el-row class="ml-10" type="flex" style="flex-direction: column;" justify="space-between">
              <span class="font-size--small">{{item.username}}</span>
              <span class="font-size--small">{{item.passtime}}</span>
            </el-row>
          </el-row>
          <el-row class="jokeText max-width ml-20 text-left line-h30">{{item.text}}</el-row>
          <video class="myVideo max-width" v-if="item.type==='video'" :src="item.video" controls></video>
          <el-image class="p-10 max-width"
                    v-viewer="{toolbar:false, title: false, navbar: false, keyboard: false}"
                    v-if="item.type==='image'"
                    :src="item.image" alt="">
            <div slot="placeholder" v-loading="true"></div>
            <div slot="error" class="image-slot">
              <img src="img/no_image.jpg" alt="" width="100%">
            </div>
          </el-image>
          <el-image class="p-10 max-width"
                    v-viewer="{toolbar:false, title: false, navbar: false, keyboard: false}"
                    v-if="item.type==='gif'"
                    :src="item.gif" alt="">
            <div slot="placeholder" v-loading="true"></div>
            <div slot="error" class="image-slot">
              <img src="img/no_image.jpg" alt="" v-viewer="{toolbar:false, title: false, navbar: false, keyboard: false}" width="100%">
            </div>
          </el-image>
          <el-row type="flex" justify="space-around" class="ptb-20">
            <el-badge :value="item.up" class="item">
              <el-button @click="getJokeDetail(item.soureid)" size="small">赞过</el-button>
            </el-badge>
            <el-badge :value="item.comment" class="item">
              <el-button @click="getJokeDetail(item.soureid)" size="small">
                <!--<i class="el-icon-s-comment font-size&#45;&#45;medium"></i>-->
                评论
              </el-button>
            </el-badge>
            <el-badge :value="item.up" class="item">
              <el-button @click="getJokeDetail(item.soureid)" size="small">踩过</el-button>
            </el-badge>
          </el-row>
          <el-divider></el-divider>
        </el-row>
      </el-row>
      <el-row v-if="jokes.length===0" class="text-center">
        <el-row v-for="item in textJokes" :key="item.hashId">
          <el-row class="jokeText text-left line-h30">{{item.content}}</el-row>
          <el-divider></el-divider>
        </el-row>
      </el-row>
    </scroller>
    <el-dialog
        :visible.sync="dialogPreviewVisible"
        width="40%">
      <el-image :src="previewSrc">
        <div slot="placeholder" v-loading="true"></div>
        <div slot="error" class="image-slot">
          <img src="img/no_image.jpg" v-viewer="{toolbar:false, title: false, navbar: false, keyboard: false}" alt="" width="100%">
        </div>
      </el-image>
    </el-dialog>
    <!--<el-row class='getMore p-40' v-loading="true">-->
      <!--<el-button type="primary" @click="getData">刷新</el-button>-->
      <!--<el-button type="primary" @click="getRealData">最新段子</el-button>-->
    <!--</el-row>-->
  </el-row>
</template>
<script>
import { getRandomJoke, getSingleJoke } from '@/api/apiopen'
import { getJoke } from '@/api/juhe'

export default {
  name: 'joke',
  data () {
    return {
      loading: false,
      jokes: [],
      textJokes: [],
      dialogPreviewVisible: false,
      previewSrc: ''
    }
  },
  created () {
    this.getJoke()
  },
  mounted () {
    this.init()
  },
  methods: {
    init () { //兼容pc
      let _this = this
      window.onmousewheel = function (e) {
        if (e.wheelDelta < 0) {// 固定值120 向上滚为-120 向下是120
          _this.$refs.jokeScroller && (_this.$refs.jokeScroller.scrollBy(0, 30, false))//避免ref中存在scrollerBottom
        } else {
          _this.$refs.jokeScroller && (_this.$refs.jokeScroller.scrollBy(0, -30, false))
        }
      }
    },
    getJoke () {
      this.loading = true
      getRandomJoke({
        param: {}
      }).then(res => {
        this.$refs.jokeScroller.finishPullToRefresh()// 关闭下拉刷新
        this.loading = false
        console.log('段子列表', res)
        if (res.code === 200) {
          this.jokes = res.data
        } else {
          this.$message.error(res)
        }
      }).catch(error => {
        this.loading = false
        this.$message.error('段子获取失败')
        console.log(error)
      })
    },
    refresh () {
      this.getJoke()
    },
    getTextJoke () {
      getJoke({
        param: {}
      }).then(res => {
        console.log('笑话', res)
        if (res.error_code === 0) {
          this.textJokes = res.result.data
        } else {
          this.$message.error(res.reason)
        }
      }).catch(error => {
        console.log(error)
      })
    },
    getJokeDetail (sid) {
      getSingleJoke({
        param: {
          sid: sid
        }
      }).then(res => {
        console.log('段子祥情', res)
      }).catch(res => {
        this.$message.error('获取详情失败')
        console.log('获取详情失败', res)
      })
    },
    handlePreview (src) {
      console.log('预览地址', src)
      this.dialogPreviewVisible = true
      this.previewSrc = src
    },
  }
}
</script>
<style lang="scss">
.joke {
  background-color: #fff;
  height: 100vh;

  .jokeText {
    width: 80%;
    max-width: 600px;
    margin: 0 auto;
    padding: 20px 0;
  }

  .myVideo {
    width: 100%;
    max-width: 600px;
  }

  .el-image {
    width: 100%;
    max-width: 600px;

    .image-slot {
      height: 100px;
    }
  }
}


@media screen and (max-device-width: 400px) { //适应手机的css
  // 真实宽度小于400px (非web端窗口大小)
  .joke {
    .max-width {
      max-width: 375px;
    }
  }
}
</style>
